import { createWebHistory, createRouter } from "vue-router";
import { RouteRecordRaw } from "vue-router";

const routes: Array<RouteRecordRaw> = [
  {
    path: "/",
    alias: "/products",
    name: "products",
    component: () => import("./components/product/ProductList.vue"),
  },
  {
    path: "/products/:id",
    name: "product-details",
    component: () => import("./components/product/ProductDetails.vue"),
  },
  {
    path: "/add",
    name: "add",
    component: () => import("./components/product/AddProduct.vue"),
  },
  {
    path: "/category",
    name: "category",
    component: () => import("./components/category/CategoryList.vue"),
  },
  {
    path: "/category/:id",
    name: "category-details",
    component: () => import("./components/category/CategoryDetails.vue"),
  },
  {
    path: "/category/add",
    name: "add-category",
    component: () => import("./components/category/AddCategory.vue"),
  },
  {
    path: "/login",
    name: "login",
    component: () => import("./components/auth/Login.vue"),
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

router.beforeEach((to, from, next) => {
  const publicPages = ["/login"];
  const authRequired = !publicPages.includes(to.path);
  const loggedIn = localStorage.getItem("user");

  if (authRequired && !loggedIn) {
    next("/login");
  } else {
    next();
  }
});

export default router;
